/* molecules */
.box {
  align-items: center;
  box-sizing: border-box;
  color: #181715;
  display: flex;
  flex-basis: 100%;
  flex-flow: column nowrap;
  flex-grow: 0;
  flex-shrink: 0;
  justify-content: flex-start;
  padding: 20px;
  position: relative;
  text-align: center;
  text-decoration: none;
  transition: background-color .25s;
}

.box:link {
  color: #181715;
  text-decoration: none;
}

.box:visited {
  color: #181715;
  text-decoration: none;
}

.box:hover {
  color: #181715;
  text-decoration: none;
}

.box:focus {
  color: #181715;
  text-decoration: none;
}

.box:not(:first-child) {
  margin-top: 80px;
}

.box:hover {
  background-color: #f1f1f1;
}

.box.has-color {
  background-color: #00a485;
  color: #ffffff;
}

.box.has-color:link {
  color: #ffffff;
  text-decoration: none;
}

.box.has-color:visited {
  color: #ffffff;
  text-decoration: none;
}

.box.has-color:hover {
  color: #ffffff;
  text-decoration: none;
}

.box.has-color:focus {
  color: #ffffff;
  text-decoration: none;
}

.box.has-color:hover {
  background-color: #002f2f;
}

.box--header {
  flex: 0 0 auto;
  margin-top: 20px;
}

.box--content {
  margin-top: 20px;
}

.box--link {
  display: block;
  margin-top: 20px;
}

.box:hover .box--link {
  margin-left: 80px;
  transition: margin .25s;
}

.box-info {
  text-align: center;
  align-items: center;
  color: #181715;
  display: flex;
  flex-flow: column nowrap;
  padding: 40px;
  position: relative;
  text-align: center;
  text-decoration: none;
  transition: background-color .25s;
}

.box-info [class*="has-separator"] {
  display: block;
  margin-top: 40px;
  padding-top: 40px;
  position: relative;
}

.box-info [class*="has-separator"]:before {
  left: 50%;
  margin-left: -12.5%;
  right: auto;
  border-top: 2px solid #ffffff;
  content: "";
  height: 2px;
  position: absolute;
  top: 0;
  width: 25%;
}

.box-info:hover {
  background-color: #f1f1f1;
}

.box-info.has-color {
  background-color: #00a485;
  color: #ffffff;
}

.box-info.has-color:hover {
  background-color: #002f2f;
}

.box-info--image {
  border-radius: 100%;
  overflow: hidden;
  max-width: 96px;
}

.box-info--header {
  flex: 0 0 auto;
  margin-top: 20px;
