html.is-locked-by-hamburger-menu,
body.is-locked-by-hamburger-menu {
  overflow: hidden;
  position: fixed;
}

.hamburger-menu {
  background-color: #ffffff;
  bottom: 0;
  margin-right: 0;
  max-width: 90%;
  overflow: hidden;
  position: fixed;
  right: -30em;
  top: 0;
  transition: right 0.3s;
  width: 30em;
  z-index: 6;
}

@media only screen and (min-width: 1024px) {
  .hamburger-menu {
    display: none;
  }
}

.hamburger-menu--inner {
  height: 100%;
  overflow-y: scroll;
}

.hamburger-menu--links {
  margin: 90px 20px 0;
  padding: 0;
  text-align: center;
}

.hamburger-menu--links a:link {
  color: #00a485;
  text-decoration: none;
}

.hamburger-menu--links a:visited {
  color: #00a485;
  text-decoration: none;
}

.hamburger-menu--links a:hover {
  color: #00a485;
  text-decoration: none;
}

.hamburger-menu--links a:focus {
  color: #00a485;
  text-decoration: none;
}

.hamburger-menu--links a:hover {
  color: #ff6620;
}

.hamburger-menu--group {
  list-style: none;
  margin-top: 20px;
  padding-bottom: 20px;
}

.hamburger-menu--group:not(:last-child) {
  border-bottom: 1px solid #d2d2d2;
}

.hamburger-menu--link {
  border-top: 1px solid #f1f1f1;
  display: block;
  font-family: "Raleway-Semibold", "Arial", sans-serif;
  padding-bottom: 20px;
  padding-top: 20px;
}

.hamburger-menu--toggle {
  cursor: pointer;
  height: 30px;
  position: absolute;
  right: 4%;
  top: 38px;
  transform: rotate(0deg);
  transition: 0.3s;
  width: 32px;
  z-index: 7;
}

@media only screen and (min-width: 1024px) {
  .hamburger-menu--toggle {
    display: none;
  }
}

.hamburger-menu--toggle span {
  background: #002f2f;
  display: block;
  height: 3px;
  left: 0;
  opacity: 1;
  position: absolute;
  transform: rotate(0deg);
  transition: 0.3s;
  width: 100%;
}

.hamburger-menu--toggle span:nth-child(1) {
  top: 0;
}

.hamburger-menu--toggle span:nth-child(2),
.hamburger-menu--toggle span:nth-child(3) {
  top: 11px;
}

.hamburger-menu--toggle span:nth-child(4) {
  top: 22px;
}

.hamburger-menu--toggle.is-open span {
  background: #002f2f;
}

.hamburger-menu--toggle.is-open span:nth-child(1) {
  left: 50%;
  top: 15px;
  width: 0;
}

.hamburger-menu--toggle.is-open span:nth-child(2) {
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

.hamburger-menu--toggle.is-open span:nth-child(3) {
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

.hamburger-menu--toggle.is-open span:nth-child(4) {
  left: 50%;
  top: 14px;
  width: 0;
}

.hamburger-menu--overlay {
  bottom: 0;
  height: 100%;
  position: absolute;
  top: 0;
  visibility: hidden;
  width: 100%;
  z-index: 5;
}

.hamburger-menu--overlay.is-visible {
  background-color: rgba(24, 23, 21, 0.85);
  cursor: pointer;
  transition: background 0.3s;
  visibility: visible;
}

.hamburger-menu.is-open {
  right: 0;
}

